<h2 id="url-autolinking" tabindex="-1"><a class="header-anchor" href="#url-autolinking" aria-hidden="true">#</a> URL autolinking</h2>
<p>GFM will autolink standard URLs, so if you want to link to a URL (instead of setting link text), you can simply enter the URL and it will be turned into a link to that URL</p>
